import React, { FC, memo } from 'react';
import styles from './index.module.scss';

interface SymbolListProps {
    borderColor: string;
    background: string;
    name: string;
}

const SymbolList: FC<SymbolListProps> = ({ borderColor, background, name }) => {
    return (
        <div>
            <span
                className={styles.symbol}
                style={{
                    borderColor,
                    background
                }}
            />
            <span>{name}</span>
        </div>
    );
};

export default memo(SymbolList);
