import React, { useRef, useEffect, useCallback } from 'react';
import echarts from 'echarts';
import { observer } from 'mobx-react-lite';
import styles from './index.module.scss';
import data from '../../data/nodes_links0316.json';
import { baseOption } from './const';
import {
    getOption1,
    getDataWithoutHidden,
    initNodes,
    getOption2,
    getOption4,
    processExpand
} from './processOpts';
import { useGlobalStore } from '../../comm/hooks';

//  时间轴keys
const timeLineKeys = Object.keys(data) as Array<keyof typeof data>;

//  初始化data，给node一个是否展开的标记
initNodes(data, timeLineKeys);

baseOption.timeline.data = timeLineKeys;

//  去掉隐藏的点和线后的data
let dataWithoutHidden: any;

const Graph = () => {
    const divElRef = useRef(null);
    const currentTimeLineKey = useRef(timeLineKeys[0]);
    const myChart = useRef<null | echarts.ECharts>(null);
    const {
        setCurrentDisplayNodes,
        selectedState,
        tabState,
        isInfoBoxShow,
        setPersonInfo,
        submitSearchString,
        selectedLineState,
        limitCount
    } = useGlobalStore();

    //  设置值为当前屏幕中显示的点
    const updateAsyInfectList = useCallback(() => {
        setCurrentDisplayNodes(
            dataWithoutHidden[currentTimeLineKey.current].nodes
        );
    }, [setCurrentDisplayNodes]);

    const nodeClickEventChange = useCallback(
        (optionStr: string) => {
            myChart.current!.off('click');
            myChart.current!.on('click', function(e: any) {
                const { id, expand, category } = e.data;

                const nodeNewCategory =
                    category === 0 || category === 2 || category === 3;

                if (e.dataType === 'node' && nodeNewCategory) {
                    processExpand(dataWithoutHidden, expand, id);

                    optionStr === 'getOption1'
                        ? getOption1(dataWithoutHidden, selectedLineState)
                        : getOption2(dataWithoutHidden, selectedState);

                    const options = getOption4(
                        dataWithoutHidden,
                        submitSearchString
                    );

                    const option = {
                        baseOption,
                        options
                    };
                    myChart.current!.setOption(option);

                    updateAsyInfectList();
                }
            });
        },
        [
            updateAsyInfectList,
            selectedLineState,
            selectedState,
            submitSearchString
        ]
    );

    //  tab改变后点和线的样式重绘
    useEffect(() => {
        if (myChart.current) {
            setTimeout(() => {
                dataWithoutHidden = getDataWithoutHidden(limitCount);

                let onFn;
                if (!tabState) {
                    getOption1(dataWithoutHidden, selectedLineState);
                    //  重新注册点的点击事件
                    nodeClickEventChange('getOption1');
                    onFn = () => {
                        nodeClickEventChange('getOption1');
                    };
                } else {
                    getOption2(dataWithoutHidden, selectedState);
                    nodeClickEventChange('getOption2');
                    onFn = () => {
                        nodeClickEventChange('getOption2');
                    };
                }

                const offFn = () => {
                    myChart.current!.off('click');
                };

                //  高亮搜索的点及其直接关联的点和线
                const options = getOption4(
                    dataWithoutHidden,
                    submitSearchString,
                    offFn,
                    onFn
                );

                const option = {
                    baseOption,
                    options
                };

                myChart.current!.setOption(option);
            }, 300);
        }
    }, [
        limitCount,
        tabState,
        selectedState,
        updateAsyInfectList,
        selectedLineState,
        nodeClickEventChange,
        submitSearchString
    ]);

    //  resize
    useEffect(() => {
        if (myChart.current) {
            myChart.current.resize();
        }
    }, [isInfoBoxShow]);

    useEffect(() => {
        if (!myChart.current) {
            dataWithoutHidden = getDataWithoutHidden(limitCount);

            const dom = divElRef.current;
            const chartIns = echarts.init(dom!);
            myChart.current = chartIns;

            updateAsyInfectList();

            const options = getOption1(dataWithoutHidden, selectedLineState);

            nodeClickEventChange('getOption1');

            chartIns.on('timelinechanged', function(e: any) {
                currentTimeLineKey.current = timeLineKeys[e.currentIndex];
                updateAsyInfectList();
            });

            chartIns.on('mouseover', function(e: any) {
                if (e.dataType === 'node') {
                    const { id, experience } = e.data;
                    setPersonInfo({ id, experience });
                }
            });

            chartIns.on('mouseout', function(e: any) {
                if (e.dataType === 'node') {
                    setPersonInfo(null);
                }
            });

            const option = {
                baseOption,
                options
            };

            chartIns.setOption(option, true);
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [setPersonInfo, updateAsyInfectList, selectedLineState]);

    return <div ref={divElRef} className={styles.container}></div>;
};

export default observer(Graph);
