import React from 'react';
import classnames from 'classnames';
import { observer } from 'mobx-react-lite';
import ArrowLeft from '../../img/left.png';
import ArrowRight from '../../img/right.png';
import styles from './index.module.scss';
import { useGlobalStore } from '../../comm/hooks';

const FoldButton = () => {
    const { setIsInfoBoxShow, isInfoBoxShow } = useGlobalStore();

    return (
        <button
            onClick={setIsInfoBoxShow}
            className={classnames(
                styles.container,
                styles[isInfoBoxShow ? 'not-hidden' : 'hidden']
            )}
        >
            <img src={isInfoBoxShow ? ArrowRight : ArrowLeft} alt='' />
        </button>
    );
};

export default observer(FoldButton);
