import { observable, action } from 'mobx';
import { createContext } from 'react';

interface PersonInfo {
    id: string;
    experience: string[];
}

class GlobalStore {
    @observable
    selectedState: number[] = [0];

    @action
    setSelectState = (state: number[]) => {
        this.selectedState = state;
    };

    @observable
    tabState = 0;

    @action
    setTabState = (state: number) => {
        this.tabState = state;
    };

    @observable
    isInfoBoxShow = true;

    @action
    setIsInfoBoxShow = () => {
        this.isInfoBoxShow = !this.isInfoBoxShow;
    };

    @observable
    personInfo: PersonInfo | null = null;

    @action
    setPersonInfo = (info: PersonInfo | null) => {
        this.personInfo = info;
    };

    @observable
    currentDisplayNodes: any[] = [];

    @action
    setCurrentDisplayNodes = (nodes: any[]) => {
        this.currentDisplayNodes = nodes;
    };

    @observable
    searchString = '';

    @action
    setSearchString = (str: string) => {
        this.searchString = str;
    };

    @observable
    searchBoxListDisplayState = false;

    @action
    setSearchBoxListDisplayState = (state: boolean) => {
        this.searchBoxListDisplayState = state;
    };

    @observable
    submitSearchString = '';

    @action
    setSubmitSearchString = () => {
        this.submitSearchString = this.searchString;
    };

    @observable
    selectedLineState = [0, 1, 2, 3, 4, 5];

    @action
    setSelectLineState = (state: number[]) => {
        this.selectedLineState = state;
    };

    @observable
    limitCount = 2;

    @action
    setLimitCount = (count: number) => {
        this.limitCount = count;
    }
}

export default createContext(new GlobalStore());
