import React from 'react';
import classnames from 'classnames';
import { observer } from 'mobx-react-lite';
import styles from './index.module.scss';
import Tab1Img from '../../../../img/tab1.png';
import Tab2Img from '../../../../img/tab2.png';
import { useGlobalStore } from '../../../../comm/hooks';


const TabHeader = () => {
    const { setTabState, tabState } = useGlobalStore();

    return (
        <div className={styles.container}>
            <button
                onClick={() => {
                    setTabState(0);
                }}
                className={!tabState ? styles.active : ''}
            >
                <img src={Tab1Img} alt='' />
                接触关系
            </button>
            <button
                onClick={() => {
                    setTabState(1);
                }}
                className={tabState ? styles.active : ''}
            >
                <img src={Tab2Img} alt='' />
                人员分类
            </button>
            <div
                className={classnames(
                    styles.line,
                    styles[!tabState ? 'line-left' : 'line-right']
                )}
            />
        </div>
    );
};

export default observer(TabHeader);
