import React, { useEffect, useRef } from 'react';
import { SearchOutlined } from '@ant-design/icons';
import { observer } from 'mobx-react-lite';
import styles from '../../index.module.scss';
import { useGlobalStore } from '../../../../../../comm/hooks';

const Input = () => {
    const {
        setSearchString,
        searchString,
        setSearchBoxListDisplayState,
        setSubmitSearchString
    } = useGlobalStore();
    const inputRef = useRef<HTMLInputElement | null>(null);

    useEffect(() => {
        if (searchString) {
            inputRef.current!.focus();
        }
    }, [searchString]);

    return (
        <div>
            <input
                ref={inputRef}
                className={styles.input}
                placeholder='输入人员编号'
                onChange={function(this: any, e) {
                    setSearchString(e.target.value);
                }}
                value={searchString}
                type='text'
                onFocus={() => {
                    setSearchBoxListDisplayState(true);
                }}
                onBlur={() => {
                    setSearchBoxListDisplayState(false);
                }}
                onKeyDown={e => {
                    if (e.keyCode === 13) {
                        setSubmitSearchString();
                    }
                }}
            />
            <SearchOutlined
                onClick={() => {
                    setSubmitSearchString();
                }}
                className={styles.icon}
            />
        </div>
    );
};

export default observer(Input);
