import React from 'react';
import { observer } from 'mobx-react-lite';
import styles from './index.module.scss';
import { linkState } from '../../../Graph/const';
import LineList from '../LineList';
import { Switch } from 'antd';
import { useGlobalStore } from '../../../../comm/hooks';

const LineLists = () => {
    const { selectedLineState, setSelectLineState } = useGlobalStore();

    const handleSwitch = (val: boolean, category: number) => {
        const newSelectedState = selectedLineState.slice(0);
        if (val) {
            newSelectedState.push(category);
        } else {
            const index = selectedLineState.indexOf(category);
            newSelectedState.splice(index, 1);
        }
        setSelectLineState(newSelectedState);
    };

    return (
        <div className={styles.container}>
            {linkState.map((val, index) => {
                return (
                    <div key={val.name} className={styles['line-lists']}>
                        <LineList background={val.color} name={val.name} />

                        <Switch
                            defaultChecked={selectedLineState.includes(index)}
                            className={styles.switch}
                            onChange={e => handleSwitch(e, index)}
                        />
                    </div>
                );
            })}
        </div>
    );
};

export default observer(LineLists);
