import React from 'react';
import { observer } from 'mobx-react-lite';
import styles from './index.module.scss';
import { useGlobalStore } from '../../../../comm/hooks';
import Ruler from '../Ruler';

const AsyInfect = () => {
    const { currentDisplayNodes } = useGlobalStore();

    return (
        <div className={styles.container}>
            <Ruler />
            <h2>高风险密切接触者</h2>
            <div>
                {currentDisplayNodes
                    .filter((node: any) => node.category === 1)
                    .map(d => (
                        <div key={d.id}>{d.id}</div>
                    ))}
            </div>
        </div>
    );
};

export default observer(AsyInfect);
