import React from 'react';
import { observer } from 'mobx-react-lite';
import classnames from 'classnames';
import styles from '../../index.module.scss';
import { useGlobalStore } from '../../../../../../comm/hooks';

const SearchList = () => {
    const {
        searchString,
        currentDisplayNodes,
        setSearchString,
        searchBoxListDisplayState
    } = useGlobalStore();

    return (
        <div
            className={classnames(
                styles['search-list-container'],
                styles[searchBoxListDisplayState ? 'list-show' : 'list-hidden']
            )}
        >
            <ul>
                {currentDisplayNodes
                    .filter((node: any) => node.id.indexOf(searchString) !== -1)
                    .map(d => (
                        <li
                            key={d.id}
                            onClick={() => {
                                setSearchString(d.id);
                            }}
                        >
                            {d.id}
                        </li>
                    ))}
            </ul>
        </div>
    );
};

export default observer(SearchList);
