import React, {
    useRef,
    useCallback,
    useState,
    useMemo,
    useEffect
} from 'react';
import styles from './index.module.scss';
import { useGlobalStore } from '../../../../comm/hooks';
import { observer } from 'mobx-react-lite';

const getEmptyArray = (len: number) => {
    const emptyArray: number[] = [];
    for (let i = 0; i < len; i++) {
        emptyArray.push(Math.random());
    }
    return emptyArray;
};

const throttle = (func: any, wait: number) => {
    var context, args;
    var previous = 0;

    return function(this: any) {
        var now = +new Date();
        context = this;
        args = arguments;
        if (now - previous > wait) {
            func.apply(context, args);
            previous = now;
        }
    };
};

const Ruler = () => {
    const rulerRef = useRef<HTMLDivElement | null>(null);
    const cursorRef = useRef<HTMLSpanElement | null>(null);
    const { setLimitCount, limitCount } = useGlobalStore();
    const currCount = useRef(limitCount);
    const [currCountShow, setCurrCountShow] = useState(limitCount);

    const setCursorLeft = useCallback((count: number) => {
        cursorRef.current!.style.left = `calc(33% * 0.2 * ${count} - 6px)`;
    }, []);

    useEffect(() => {
        setCursorLeft(limitCount);
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    const handleMouseDown = useCallback(
        (e: React.MouseEvent<HTMLSpanElement, MouseEvent>) => {
            if (e.button === 0) {
                rulerRef.current!.addEventListener(
                    'mousemove',
                    handleMouseMove
                );
                rulerRef.current!.addEventListener('mouseup', handleMouseUp);
            }
            e.preventDefault();
        },
        // eslint-disable-next-line react-hooks/exhaustive-deps
        []
    );

    const handleMouseMove = useMemo(() => {
        const eventFn = (e: any) => {
            const { clientX } = e;
            const { x, width } = rulerRef.current!.getBoundingClientRect();

            const offsetX = clientX - x;
            if (clientX >= x + 36 && clientX <= x + width - 3) {
                const count = Math.round((offsetX / width) * 15);
                currCount.current = count;
                setCursorLeft(count);
                setCurrCountShow(count);
            }
        };
        return throttle(eventFn, 100);
    }, [setCursorLeft]);

    const handleMouseUp = useCallback(() => {
        rulerRef.current!.removeEventListener('mousemove', handleMouseMove);
        rulerRef.current!.removeEventListener('mouseup', handleMouseUp);

        setLimitCount(currCount.current);
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    return (
        <div className={styles.container}>
            <div className={styles.title}>
                <span>密切接触者筛选</span>
                <span>
                    接触人数大于
                    <span className={styles.count}>{currCountShow}</span>
                </span>
            </div>
            <div id='ruler' className={styles.ruler} ref={rulerRef}>
                {getEmptyArray(3).map((d, idx) => (
                    <div key={d + idx} className={styles.cm}>
                        {getEmptyArray(4).map((d2, idx2) => (
                            <div key={d2 + idx2} className={styles.mm}></div>
                        ))}
                    </div>
                ))}
                <div className={styles.cm}></div>
                <span
                    ref={cursorRef}
                    draggable
                    onMouseDown={handleMouseDown}
                    className={styles.cursor}
                ></span>
            </div>
        </div>
    );
};

export default observer(Ruler);
