import { categories, linkState } from './const';

let sourceData: any;
let timeLineKeys: string[];
const focusNodesId = [0, 2, 3];

const getSymbolSize = (id: string, key: string) => {
    const { links } = sourceData[key];

    const sourceLinksCount = links.filter((link: any) => link.source === id)
        .length;

    if (sourceLinksCount === 0) return 10;
    // const val = Math.pow(sourceLinksCount, 1.01);

    // return val < 10 ? val + 10 : val;
    return sourceLinksCount;
};

const getCommOption = (
    key: string,
    data: any,
    links: any,
    focusNodeAdjacency = true
) => ({
    title: {
        text: key
    },
    series: [
        {
            name: '疫情传染网络图',
            type: 'graph',
            layout: 'force',
            data,
            links,
            categories,
            animation: false,
            roam: true,
            label: {
                position: 'right'
            },
            force: {
                repulsion: 80,
                edgeLength: [30, 150]
            },
            focusNodeAdjacency,
            draggable: true,
            lineStyle: {
                color: 'source',
                curveness: 0.3
            }
        }
    ]
});

export const getOption1 = (allData: any, selectedLineState: number[]) =>
    timeLineKeys.map(key => {
        const { links, nodes } = allData[key];

        nodes.forEach((node: any) => {
            const {
                color: color0,
                borderColor: borderColor0
            } = categories[0].itemStyle;
            const {
                color: color6,
                borderColor: borderColor6
            } = categories[5].itemStyle;

            //  确诊、死亡、治愈用红色,密切接触者、解除医学观察、无症状感染用白色
            const nodeFocusState = focusNodesId.includes(node.category);

            node.itemStyle = {
                color: nodeFocusState ? color0 : color6,
                borderColor: nodeFocusState ? borderColor0 : borderColor6
            };
            node.itemStyle.borderWidth = 2;

            //  根据该点target的数量确定点的大小，包含隐藏点
            node.symbolSize = getSymbolSize(node.id, key);
            node.value = node.symbolSize;
            node.label = {
                show: false
            };
            node.x = node.y = null;
        });

        links.forEach((link: any) => {
            //  根据线图例的状态确定线的颜色
            link.lineStyle = {
                color: selectedLineState.includes(link.state)
                    ? linkState[link.state].color
                    : '#333',
                width: 2
            };
            link.realValue =
                typeof link.realValue === 'undefined'
                    ? link.value
                    : link.realValue;
            link.value = 5;
        });

        return getCommOption(key, nodes, links);
    });

export const getOption2 = (allData: any, selectedState: number[]) =>
    timeLineKeys.map(key => {
        const { links, nodes } = allData[key];

        nodes.forEach((node: any) => {
            //  根据点的分类设置点的颜色， 根据点图例画出对应的点的颜色是否高亮
            const { color, borderColor } = categories[node.category].itemStyle;
            node.itemStyle = {
                color,
                borderColor,
                borderWidth: selectedState.indexOf(node.category) !== -1 ? 2 : 0
            };
            node.symbolSize = getSymbolSize(node.id, key);
            node.value = node.symbolSize;
            node.label = {
                show: false
            };
            node.x = node.y = null;
        });

        //  该tab下线的统一颜色
        links.forEach((link: any) => {
            link.lineStyle = {
                color: '#989898',
                width: 2
            };
            link.value = 5;
        });

        return getCommOption(key, nodes, links);
    });

export const getOption4 = (allData: any, val: string, offFn?: any, onFn?: any) =>
    timeLineKeys.map(key => {
        const { links, nodes } = allData[key];

        let lock = false;
        if (val === '') {
            onFn && onFn();
            links.forEach((link: any) => {
                link.lineStyle.opacity = 1;
            });
            nodes.forEach((node: any) => {
                node.itemStyle.opacity = 1;
            });
        } else {
            //  降低所有线和线的透明度
            links.forEach((link: any) => {
                link.lineStyle.opacity = 0.1;
            });
            nodes.forEach((node: any) => {
                node.itemStyle.opacity = 0.1;
            });
            lock = true;

            const highlightNodes = new Set();

            nodes.forEach((node: any) => {
                //  Id与搜索字符串匹配的点
                if (node.id === val) {
                    offFn && offFn();
                    links.forEach((link: any) => {
                        //  与该点相关联的线
                        if (
                            link.source === node.id ||
                            link.target === node.id
                        ) {
                            link.lineStyle.opacity = 1;

                            //  线两端的点存入Set中去重
                            highlightNodes.add(link.target);
                            highlightNodes.add(link.source);
                        }
                    });
                    highlightNodes.add(node.id);
                }
            });

            //  标记高亮的点
            const highlightNodesArr = Array.from(highlightNodes);
            nodes.forEach((node: any) => {
                if (highlightNodesArr.includes(node.id)) {
                    node.itemStyle.opacity = 1;
                }
            });
        }

        return getCommOption(key, nodes, links, !lock);
    });

export const getDataWithoutHidden = (limitCount: number) => {
    const newData = Object.create(null);

    timeLineKeys.forEach(key => {
        const { nodes, links } = sourceData[key];

        //  找到为source或者作为target出现过 limitCount 次及以上的nodes
        const newNodes = nodes.filter(
            (node: any) =>
                focusNodesId.includes(node.category) ||
                links.filter((link: any) => link.target === node.id).length >=
                    limitCount
        );
        const newAddNodesId = newNodes.map((node: any) => node.id);

        //  找出不满足条件的node
        const nodeId = nodes
            .filter((node: any) => focusNodesId.includes(node.category))
            .filter((node: any) => {
                const count =
                    links.filter(
                        (link: any) =>
                            link.target === node.id &&
                            newAddNodesId.includes(link.source)
                    ).length +
                    links.filter(
                        (link: any) =>
                            link.source === node.id &&
                            newAddNodesId.includes(link.target)
                    ).length;

                return count < limitCount;
            })
            .map((node: any) => node.id);

        //  找到newNodes中对应的links
        const newLinks = links.filter(
            (link: any) =>
                newNodes.findIndex((node: any) => node.id === link.target) >
                    -1 &&
                newNodes.findIndex((node: any) => node.id === link.source) >
                    -1 &&
                !(nodeId.includes(link.source) && nodeId.includes(link.target))
        );

        const newNodesWithExpand = JSON.parse(JSON.stringify(newNodes));

        newNodes.forEach((node: any) => {
            const { expand, id } = node;
            if (expand === true) {
                //  折叠起来的点： 1)该点为事件点的目标点； 2)该点只有一次作为目标点； 3)该点不是起始点
                const newAddNodes = nodes.filter(
                    (node: any) =>
                        links.findIndex(
                            (link: any) =>
                                link.source === id && link.target === node.id
                        ) > -1 &&
                        links.filter((link: any) => link.target === node.id)
                            .length === 1 &&
                        links.filter((link: any) => link.source === node.id)
                            .length === 0
                );

                //  起始点为事件点且目标点存在于newAddNodes中
                const newAddLinks = links.filter(
                    (link: any) =>
                        link.source === id &&
                        newAddNodes.findIndex(
                            (node: any) => node.id === link.target
                        ) > -1
                );

                newNodesWithExpand.push(...newAddNodes);
                newLinks.push(...newAddLinks);
            }
        });

        newData[key] = {
            nodes: newNodesWithExpand,
            links: newLinks
        };
    });

    return newData;
};

export const initNodes = (allData: any, keys: string[]) => {
    keys.forEach(key => {
        allData[key].nodes.forEach((node: any) => {
            node.expand = false;
        });
        // allData[key].links.forEach((link: any) => {
        //     link.value = 5
        // })
    });

    sourceData = JSON.parse(JSON.stringify(allData));
    timeLineKeys = keys;
};

export const processExpand = (
    dataWithoutHidden: any,
    expand: boolean,
    id: string
) => {
    timeLineKeys.forEach(key => {
        const { nodes, links } = sourceData[key];

        if (expand === false) {
            //  折叠起来的点： 1)该点为事件点的目标点； 2)该点只有一次作为目标点； 3)该点不是起始点
            const newAddNodes = nodes.filter(
                (node: any) =>
                    links.findIndex(
                        (link: any) =>
                            link.source === id && link.target === node.id
                    ) > -1 &&
                    links.filter((link: any) => link.target === node.id)
                        .length === 1 &&
                    links.filter((link: any) => link.source === node.id)
                        .length === 0
            );

            //  起始点为事件点且目标点存在于newAddNodes中
            const newAddLinks = links.filter(
                (link: any) =>
                    link.source === id &&
                    newAddNodes.findIndex(
                        (node: any) => node.id === link.target
                    ) > -1
            );

            dataWithoutHidden[key].nodes.push(...newAddNodes);
            dataWithoutHidden[key].links.push(...newAddLinks);
        } else {
            //  需要移除的点： 1）起始点为事件点；2）只有一次作为目标点； 3）该点不是起始点
            const removeNodesId = dataWithoutHidden[key].links
                .filter((link: any) => link.source === id)
                .map((link: any) => link.target)
                .filter(
                    (id: any) =>
                        links.filter((link: any) => link.target === id)
                            .length === 1 &&
                        links.filter((link: any) => link.source === id)
                            .length === 0
                );

            dataWithoutHidden[key].nodes = dataWithoutHidden[key].nodes.filter(
                (node: any) => removeNodesId.indexOf(node.id) === -1
            );
            dataWithoutHidden[key].links = dataWithoutHidden[key].links.filter(
                (link: any) =>
                    !(
                        link.source === id &&
                        removeNodesId.indexOf(link.target) !== -1
                    )
            );
        }
        const index = dataWithoutHidden[key].nodes.findIndex(
            (node: any) => node.id === id
        );
        const index2 = sourceData[key].nodes.findIndex(
            (node: any) => node.id === id
        );

        if (index !== -1) {
            dataWithoutHidden[key].nodes[index].expand = !expand;
        }
        if (index2 !== -1) {
            sourceData[key].nodes[index2].expand = !expand;
        }
    });
};
