import React from 'react';
import { observer } from 'mobx-react-lite';
import classnames from 'classnames';
import styles from './index.module.scss';
import { useGlobalStore } from '../../comm/hooks';

const TooltipBox = () => {
    const { isInfoBoxShow, personInfo } = useGlobalStore();
    if (!personInfo) return null;

    const { id, experience } = personInfo;

    return (
        <div
            className={classnames(
                styles.container,
                styles[isInfoBoxShow ? 'not-hidden' : 'hidden']
            )}
        >
            <div>{id}</div>
            <div>
                {experience.map(val => (
                    <div key={val}>{val}</div>
                ))}
            </div>
        </div>
    );
};

export default observer(TooltipBox);
