import React, { FC } from 'react';
import { Switch } from 'antd';
import { categories } from '../../../Graph/const';
import styles from './index.module.scss';
import SymbolList from '../SymbolList';
import { useGlobalStore } from '../../../../comm/hooks';
import { observer } from 'mobx-react-lite';

interface SymbolListProps {
    categories: typeof categories;
    hasSwitch?: boolean;
}

const SymbolLists: FC<SymbolListProps> = ({ categories, hasSwitch }) => {
    const { setSelectState, selectedState } = useGlobalStore()!;

    const handleSwitch = (val: boolean, category: number) => {
        const newSelectedState = selectedState.slice(0);
        if (val) {
            newSelectedState.push(category);
        } else {
            const index = selectedState.indexOf(category);
            newSelectedState.splice(index, 1);
        }
        setSelectState(newSelectedState);
    };

    return (
        <div>
            {categories.map((val, index) => {
                return (
                    <div
                        key={val.name}
                        className={styles['symbol-list']}
                        style={{ marginBottom: '16px' }}
                    >
                        <SymbolList
                            background={val.itemStyle.color}
                            borderColor={val.itemStyle.borderColor}
                            name={val.name}
                        />
                        {hasSwitch && (
                            <Switch
                                defaultChecked={
                                    selectedState.indexOf(index) !== -1
                                }
                                className={styles.switch}
                                onChange={e => handleSwitch(e, index)}
                            />
                        )}
                    </div>
                );
            })}
        </div>
    );
};

export default observer(SymbolLists);
